export default ServicePage;

export const ServicePageContainer = styled.div`
  width: 100%;
  display: flex;
  background-color: #131111;
  color: white;

  @media only screen and (min-width: 300px) {
    .container {
      justify-content: center;
      text-align: center;
      width: 100%;
      .text-container {
        text-align: center;
        margin: 10vh auto;
        padding: 1vw;

        .dvider {
          background-color: white;
          margin: 0 40% 0 40%;
          height: 2px;
        }
      }

      .text-section {
        text-align: left;
        padding: 1vw;
        color: white;
        margin: 0 15% 0 15%;
        h1 {
          font-size: 100%;
          text-transform: uppercase;
        }
        h2 {
          text-transform: uppercase;
          letter-spacing: 1px;
          font-size: 100%;
          line-height: 4px;
          margin-top: 2vw;
        }

        #keys {
          border: solid 2px #ffffff;
          border-radius: 2vw;
          padding: 2vw;
          margin-bottom: 2vw;
        }
        #shoes {
          border-radius: 2vw;
          border: solid 2px #ffffff;
          padding: 2vw;
          margin-bottom: 2vw;
        }
        #batteries {
          border-radius: 2vw;
          border: solid 2px #ffffff;
          padding: 2vw;
          margin-bottom: 2vw;
        }

        #riding {
          border-radius: 2vw;
          border: solid 2px #ffffff;
          padding: 2vw;
          margin-bottom: 2vw;
        }
        p {
          color: #ffffff;
          font-family: "Space Mono", Sans-serif;
          font-size: 15px;
          line-height: 28px;
          display: block;
          margin-block-start: 1em;
          margin-block-end: 1em;
          margin-inline-start: 0px;
          margin-inline-end: 0px;
        }
      }
    }
  }

  @media only screen and (min-width: 750px) {
    .container {
      justify-content: center;
      text-align: center;
      width: 100%;
      .text-container {
        text-align: center;
        margin: 10vh auto;
        padding: 1vw;

        .dvider {
          background-color: white;
          margin: 0 45% 0 45%;
          height: 2px;
        }
      }

      .text-section {
        text-align: left;
        padding: 1vw;
        color: white;
        h1 {
          padding-bottom: 5vh;
          font-size: 200%;
          text-align: center;
          text-transform: uppercase;
        }
        h2 {
          letter-spacing: 4px;
          font-size: 150%;
          line-height: 4px;
          margin-top: 2vw;
        }

        #keys {
          border: solid 2px #ffffff;
          padding: 2vw;
          margin-bottom: 5vw;
        }
        #shoes {
          border: solid 2px #ffffff;
          padding: 2vw;
          margin-bottom: 5vw;
        }
        #batteries {
          border: solid 2px #ffffff;
          padding: 2vw;
          margin-bottom: 5vw;
        }

        #riding {
          border: solid 2px #ffffff;
          padding: 2vw;
          margin-bottom: 5vw;
        }
        p {
          color: #ffffff;
          font-size: 120%;
          letter-spacing: 0.5px;
          line-height: 40px;
          display: block;
          margin-block-start: 1em;
          margin-block-end: 1em;
          margin-inline-start: 0px;
          margin-inline-end: 0px;
        }
      }
    }
  }
`;
